/* --- SCSS Variables (Import or Define) --- */
// Ensure these variables are consistent with your other SCSS files.

// Color Palette
$primary-color: #0d6efd;
$secondary-color: #6c757d;
$success-color: #198754;
$warning-color: #ffc107;
$danger-color: #dc3545;
$info-color: #0dcaf0;

// Layout & UI Colors
$app-background-color: #f8f9fa;
$container-background-color: #ffffff;
$text-color-primary: #212529;
$text-color-secondary: #495057;
$border-color: #dee2e6;
$card-border-color: #e9ecef; // A slightly lighter border for cards

// Layout & Spacing
$border-radius: 8px;
$base-padding: 1.5rem; // 24px
$grid-gap: 1.5rem;
$base-margin: 1rem;
$base-shadow: 0 5px 15px rgba(0, 0, 0, 0.07);

/* --- Products Page Container --- */
.products-container {
    background-color: $container-background-color;
    border-radius: $border-radius;
    padding: $base-padding;
    border: 1px solid transparentize($border-color, 0.5);
}

/* --- Page Header --- */
.page-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap; // Allow wrapping on small screens
    gap: $base-margin;
    margin-bottom: $grid-gap;
    padding-bottom: $base-padding;
    border-bottom: 1px solid $border-color;

    .page-title {
        font-size: 1.75rem;
        font-weight: 600;
        color: darken($text-color-secondary, 20%);
    }

    .header-actions {
        display: flex;
        gap: 0.75rem;
    }
}

/* --- Filters Section --- */
.filters-section {
    padding: $base-padding;
    background-color: lighten($app-background-color, 2%);
    border-radius: $border-radius;
    border: 1px solid $card-border-color;
    margin-bottom: $grid-gap;
    overflow: hidden;
    transition: max-height 0.5s ease-out, padding 0.5s ease-out;

    // For smooth toggle animation with JS
    &.is-hidden {
        max-height: 0;
        padding-top: 0;
        padding-bottom: 0;
        border: none;
        margin-bottom: 0;
    }
}

.filters-form {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: $grid-gap;
    align-items: flex-end; // Align items to the bottom

    .filter-label {
        font-weight: 500;
        font-size: 0.9rem;
        margin-bottom: 0.5rem;
        display: block;
    }

    .filter-actions {
        display: flex;
        gap: 0.5rem;
        
        .btn {
            flex-grow: 1; // Allow buttons to fill space
        }
    }
}

/* --- Products Grid Layout --- */
.products-grid {
    display: grid;
    gap: $grid-gap;
    // Responsive grid columns
    grid-template-columns: 1fr; // 1 column by default (mobile)

    @media (min-width: 600px) {
        grid-template-columns: repeat(2, 1fr); // 2 columns for tablets
    }
    @media (min-width: 992px) {
        grid-template-columns: repeat(3, 1fr); // 3 columns for desktops
    }
    @media (min-width: 1400px) {
        grid-template-columns: repeat(4, 1fr); // 4 columns for large desktops
    }
}

/* --- Product Card Styling --- */
.product-card {
    background-color: #fff;
    border-radius: $border-radius;
    border: 1px solid $card-border-color;
    box-shadow: 0 2px 8px rgba(0,0,0,0.05);
    overflow: hidden;
    display: flex;
    flex-direction: column;
    transition: transform 0.2s ease, box-shadow 0.2s ease;

    &:hover {
        transform: translateY(-5px);
        box-shadow: $base-shadow;
    }
}

.product-image {
    width: 100%;
    padding-top: 75%; // Aspect ratio 4:3
    position: relative;
    background-color: #f0f2f5;

    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover; // Ensures image covers the area without distortion
    }
    
    .product-image-placeholder {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        .material-symbols-outlined {
            font-size: 4rem;
            color: $border-color;
        }
    }
}

.product-info {
    padding: $base-margin;
    display: flex;
    flex-direction: column;
    flex-grow: 1; // Allows info to fill the card height
}

.product-header {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 0.5rem;
    margin-bottom: 0.5rem;

    .product-name {
        font-size: 1.1rem;
        font-weight: 600;
        color: $text-color-primary;
        line-height: 1.3;
    }
    .product-sku {
        font-size: 0.8rem;
        font-weight: 500;
        color: $text-color-secondary;
        background-color: $app-background-color;
        padding: 0.15rem 0.4rem;
        border-radius: 4px;
        white-space: nowrap;
    }
}

.product-description {
    font-size: 0.85rem;
    color: $text-color-secondary;
    margin-bottom: $base-margin;
    flex-grow: 1; // Pushes details to the bottom
}

.product-details {
    display: flex;
    justify-content: space-between;
    margin-bottom: $base-margin;
    font-size: 0.9rem;
    border-top: 1px solid $card-border-color;
    padding-top: $base-margin;

    .price-label, .weight-label {
        color: $text-color-secondary;
    }
    .price-value, .weight-value {
        font-weight: 600;
        color: $text-color-primary;
    }
}

.product-status {
    margin-bottom: $base-margin;
    // Re-use status-badge styles from users.scss if in a global file
    // Or redefine here if needed
}

.product-actions {
    display: flex;
    gap: 0.5rem;
    .btn {
        flex: 1;
        padding: 0.5rem;
        font-size: 0.85rem;
    }
}


/* --- Large Modal for Product Form --- */
.modal-content.modal-large {
    max-width: 800px; // Wider modal for complex forms
}

.form-grid {
    padding: $base-padding;
    display: grid;
    gap: $base-margin $grid-gap;
    grid-template-columns: 1fr; // Single column on mobile

    @media (min-width: 768px) {
        grid-template-columns: 1fr 1fr; // Two columns on wider screens
    }

    .form-group.full-width {
        grid-column: 1 / -1; // Span across all columns
    }
}

.form-help {
    font-size: 0.8rem;
    color: $text-color-secondary;
    margin-top: 0.25rem;
    display: block;
}